table {
  font-family: arial, sans-serif;
  border-collapse: collapse;
  width: 100%;
  overflow: hidden;
}

td,
th {
  text-align: left;
  padding: 0 25px;
}
tr {
  position: relative;
  cursor: pointer;
  height: 80px;
  border-bottom: 2px solid #eee;
  transition: 0.5s ease-in-out;
}

tr:hover {
  box-shadow: 0 0 15px #1780a936;
}

.not-post {
  position: absolute;
  content: "";
  width: 100%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  justify-content: center;
  align-items: center;
}
